import service from './service'
import {
    Toast
} from 'mint-ui'
import Router from '../../router'
import { addParamsForUrl } from '../../utils/utils'
import { stat } from 'fs';

export default {
    namespaced: true,
    state: {
        isLogin: false,
        activityId: 2,
        inviter: '',
        user: {},
        statistic: {
            // 
            maxAwardAmt: 10000,
            // 
            totalAwardAmt: 0,
            // 
            chance: 0,
            // 
            rank: 0,
            // salt
            signSalt: ''
        },
        rank: {
            selfRank: {
                user: {}
            },
            rankList: []
        },
        startResult: {},
        startData: [],
        questions: {
            currentQuesitionNum: 0
        },
        endData: {},
        shareData: ''
    },
    mutations: {
        // 
        SET_USER(state, para) {
            state.user = para
            state.isLogin = true;
        },
        // 
        SET_STATISTIC(state, para) {
            state.statistic = para
        },
        // 
        SET_RANKLIST(state, para) {
            state.rank = para
        },
        // 
        startData(state, payload) {
            let data = payload.data.result
            state.startResult = data
            state.startData = data.questions
        },
        // 
        endData(state, payload) {
            let data = payload.data
            state.endData = data.result
            data.result.totalAwardAmt && (state.statistic.totalAwardAmt = data.result.totalAwardAmt);
        },
        shareData(state, payload) {
            let data = payload.data.result
            state.shareData = data.shareUrl || data.defaultShareUrl
        },
        SET_PARAMS(state, payload) {
            let { inviter, activityId } = payload
            activityId && (state.activityId = activityId);
            inviter && (state.inviter = inviter);
        },
        updateChance(state) {
            let chance = state.statistic.chance
            state.statistic.chance = --chance
        },
        updateCurrentQuestionNum(state) {
            state.questions.currentQuesitionNum = 0
            console.log(123, state.questions.currentQuesitionNum)
        },
        nextQuestion(state) {
            let currentQuesitionNum = state.questions.currentQuesitionNum
            state.questions.currentQuesitionNum = ++currentQuesitionNum
        }
    },

    actions: {
        updateChance({ commit }) {
            commit('updateChance')
        },
        updateCurrentQuestionNum({ commit }) {
            commit('updateCurrentQuestionNum')
        },
        async userShare({
            state,
            commit
        }, payload) {
            if (sessionStorage.userAnswerId) {
                let params = {
                    userAnswerId: sessionStorage.userAnswerId
                }
                let {
                    data
                } = await service.userShare(params).catch(err => {
                    Toast('')
                    return;
                })
                if (typeof(data.code) != undefined && data.code == 0) {
                    commit({
                        type: 'shareData',
                        data
                    })
                } else {
                    data.result && Toast(data.result)
                }
            } else {
                let params = {
                    activityId: state.activityId
                }
                console.log('default share--', params)
                let {
                    data
                } = await service.userDefaultShare(params).catch(err => {
                    Toast('')
                    return;
                })
                if (typeof(data.code) != undefined && data.code == 0) {
                    commit({
                        type: 'shareData',
                        data
                    })
                } else {
                    data.result && Toast(data.result)
                }
            }
        },
        // params
        checkParams({
            commit
        }, {
            params,
            activityId,
            inviter
        }) {
            try {
                params = JSON.parse(decodeURIComponent(params))
            } catch (e) {
                params = {};
            }
            activityId = params.activityId || activityId;
            inviter = params.inviter || inviter;
            // urlactivityId
            console.log('params:', activityId, inviter);
            commit('SET_PARAMS', {
                activityId,
                inviter
            });
        },
        // 
        checkLoginInApp({
            state,
            commit,
            rootState
        }, cb) {
            console.log("checkLogin", rootState.IS_APP);
            let ua = rootState.UA;
            if (ua.indexOf("closer-ios") > -1) {
                console.log("module closer-ios");
                setTimeout(() => {
                    setupWebViewJavascriptBridge(function(bridge) {
                        console.log("ios bridge", bridge)
                        if (bridge) {
                            //iostoken 
                            bridge.callHandler("getUserToken", null, function(token, responseCallback) {
                                console.log("ios token", token)
                                if (token) {
                                    Cookies.set("GroukAuth", token, {
                                        expires: 30
                                    });
                                    // setTimeout(() => {
                                    service.getUserInfoInApp().then(({
                                        data
                                    }) => {
                                        console.log("ios", data.result);
                                        if (data.result) {
                                            let userInfo = data.result;
                                            Cookies.set("user", userInfo, {
                                                expires: 30
                                            });
                                            commit('SET_USER', userInfo);
                                            cb(true)
                                        } else {
                                            cb();
                                        }
                                    }).catch(err => {
                                        Toast('')
                                        return;
                                    })
                                } else {
                                    console.log("ios jumpLogin")
                                    setupWebViewJavascriptBridge(function(bridge) {
                                        bridge.callHandler("jumpLogin", null);
                                    });
                                    cb();
                                }
                            });
                        }
                    })
                }, 500)
            } else if (ua.indexOf("closer-android") > -1) {
                console.log("closer-android")
                console.log("module android", typeof window.bridge != "undefined")
                    //
                if (typeof window.bridge != "undefined") {
                    let token = window.bridge.getUserToken(null);
                    console.log("android", token)
                    if (token) {
                        Cookies.set("GroukAuth", token, {
                            expires: 30
                        });
                        // setTimeout(() => {
                        service.getUserInfoInApp().then(({
                            data
                        }) => {
                            console.log("android", data.result);
                            if (data.result) {
                                let userInfo = data.result;
                                Cookies.set("user", userInfo, {
                                    expires: 30
                                });
                                commit('SET_USER', userInfo);
                                cb(true)
                            } else {
                                cb();
                            }
                        }).catch(err => {
                            Toast('')
                            return;
                        })

                        // }, 1000)
                    } else {
                        console.log("android jumpLogin")
                        window.bridge.jumpLogin(null);
                        cb()
                    }
                }
            } else {
                cb()
            }
        },
        // code
        async getUserInfoAndLoginWithWx({
            state,
            commit
        }, {
            code,
            inviter
        }) {
            inviter = state.inviter || inviter;
            let _params = {
                plateform: 2,
                // code
                code,
                // id
                inviter,
                protocol: "WEB_SOCKET"
            };
            // cookie
            let user = Cookies.get('user');
            let token = Cookies.get('GroukAuth');
            if (user && token) {
                console.log('user-from-cookie:', JSON.parse(user));
                commit('SET_USER', JSON.parse(user));
                return true;
            } else {
                let { data } = await service.loginWithWx(_params);
                if (data.code == 0) {
                    user = data.result.user;
                    token = data.result.token;
                    console.log('user-from-server:', user);
                    commit('SET_USER', user)
                    Cookies.set('GroukAuth', token, {
                        expires: 7
                    })
                    Cookies.set('user', user, {
                        expires: 7
                    })
                    return true;
                } else {
                    return false;
                }
            }

        },
        // 
        async getStatistic({
            commit,
            state
        }) {
            let {
                data
            } = await service.getStatistic({
                activityId: state.activityId
            });
            console.log(data.code, data.result)
            if (data.code == 0) {
                commit('SET_STATISTIC', data.result)
            } else {
                Toast('')
            }
        },
        // 
        async getRankList({
            commit,
            state
        }) {
            let {
                data
            } = await service.getRankList({
                activityId: state.activityId,
                inviteUser: state.inviter
            });
            if (data.code == 0) {
                commit('SET_RANKLIST', data.result)
            } else {
                Toast('')
            }
        },
        // 
        async startTest({
            commit,
            state
        }, payload) {
            let params = {
                activityId: state.activityId
            }
            let {
                data
            } = await service.startTest(params).catch(err => {
                Toast('')
                return;
            })
            if (typeof(data.code) != undefined && data.code == 0) {
                commit({
                    type: 'startData',
                    data
                })
            } else {
                Toast('')
                return;
            }
        },
        nextQuestion({
            state,
            commit
        }, payload) {
            commit("nextQuestion")
            if (state.questions.currentQuesitionNum > state.startData.length) return
        },
        // 
        async commitTest({
            state,
            commit
        }, payload) {
            let {
                data
            } = await service.endTest(payload).catch(err => {
                Toast('')
                return;
            })
            console.log(3, data)
            if (typeof(data.code != undefined) && data.code == 0) {
                commit({
                    type: 'endData',
                    data
                })
                Router.push({
                    name: "localResult"
                })
                window.sessionStorage.userAnswerId = state.endData.userAnswerId
            } else {
                data.result && Toast(data.result)
            }
        },
        initWxConfig({ state, rootState }) {
            let params = {
                url: location.href
            };
            service.wechatConfig(params).catch(err => {
                Toast('')
                return;
            }).then(({ data }) => {
                let wxConfig = {};
                let link = addParamsForUrl(location.origin + '/local', {
                    inviter: state.user.objectID,
                    activityId: state.activityId
                });
                if (typeof(data.code) != "undefined" && data.code == 0) {
                    wxConfig = data.result;
                } else {
                    return;
                }
                console.log('wxConfig::', link);
                if (wxConfig && wxConfig.signature && wxConfig.appId && wxConfig.nonceStr && wxConfig.timestamp) {
                    wx.config({
                        "debug": false, // ,apialertpclogpc
                        "signature": wxConfig.signature,
                        "appId": wxConfig.appId,
                        "nonceStr": wxConfig.nonceStr,
                        "timestamp": wxConfig.timestamp,
                        jsApiList: ['onMenuShareTimeline', 'onMenuShareAppMessage'] // JS
                    });
                    wx.ready(function() {
                        console.log("ready")
                            // 
                        wx.onMenuShareAppMessage({
                            title: '', // 
                            desc: '', // 
                            link, // JS
                            imgUrl: 'http://file.tiejin.cn/public/9Ykg0XgzgX/share_img.png', // 
                            type: '', // ,musicvideolinklink
                            dataUrl: '', // typemusicvideo
                            success: function() {
                                // 
                                Toast('~')
                            }
                        })

                        // 
                        wx.onMenuShareTimeline({
                                title: '', // 
                                link, // JS
                                imgUrl: 'http://file.tiejin.cn/public/9Ykg0XgzgX/share_img.png', // 
                                success: function() {
                                    // 
                                    Toast('~')
                                }
                            })
                            // configreadyconfigconfigreadyready
                    })
                    wx.error(function(res) {
                        console.log("error", res)
                            // configerrorconfigdebugresSPA
                    });
                }
            })
        }
    }
}